var Simplex = require('simplex-noise')
var createRandom = require('@tatumcreative/random')
var lerp = require('lerp')
var ref = require('@tatumcreative/color');
var hslToStyle = ref.hslToStyle;
var TAU = Math.PI * 2
var ease = require('eases/circ-out')
var shortcuts = require("./shortcuts")

;(function main () {
  var config = {
    maxDrips: 5000,
    dripsColor: 'rgba(50,255,255,0.1)',
    backgroundColor: hslToStyle(0.65, 0.4, 0.05),
    dimmingColor: hslToStyle(0.65, 0.4, 0.05, 0.03),
    dimmingChance: 0.05,
    dripsPerFrame: 2,
    dripGeneratorWidth: 20, //pixels
    dripStep: 2, //pixels
    dripSize: [3, 20],
    dripDirection: 0.5,
    dripDirectionalBias: 0.8,
    streams: [
      { center: 0.25, hue: 0.35 },
      { center: 0.5, hue: 0.5 },
      { center: 0.75, hue: 0.65 }
    ],
    dripHSLARange: [
      [0.0, 0.15],
      [0.8, 1.0],
      [0.5, 0.5],
      [0.1, 0.2]
    ],
    baseSlope: 0.00,
    landscapeXYScale: 0.005,
    landscapeShiftSpeed: 0.0001
  }

  var ref = prepCanvasAndGetCtx(config);
  var canvas = ref[0];
  var ctx = ref[1];
  var seed = window.location.hash || Math.random()
  var random = createRandom(seed)
  shortcuts(seed)

  var current = {
    canvas: canvas,
    random: random,
    simplex: new Simplex(random),
    ctx: ctx,
    drips: [],
    time: 0
  }

  function loop() {
    current.time++
    addDripStream(config, current)
    moveDrips(config, current)
    drawScreenDimmer(config, current)
    drawDrips(config, current)
    requestAnimationFrame(loop)
  }
  loop()
})()

function addDripStream(config, current) {
  var streams = config.streams;

  streams.forEach(function (stream) {
    addNewDrips(config, current, stream.center, stream.hue)
  })
}

function addNewDrips (config, current, center, hue) {
  var dripsPerFrame = config.dripsPerFrame;
  var maxDrips = config.maxDrips;
  var dripGeneratorWidth = config.dripGeneratorWidth;
  var dripSize = config.dripSize;
  var dripHSLARange = config.dripHSLARange;
  var drips = current.drips;
  var random = current.random;
  var w = window.innerWidth

  for (var i = 0; i < dripsPerFrame; i++) {
    if(drips.length >= maxDrips) {
      break
    }
    // Center new drips along the top of the screen.
    var x = w * center + random() * dripGeneratorWidth - dripGeneratorWidth / 2
    var y = 0
    var hsla = dripHSLARange.map(function (range) { return random.apply(void 0, range); })
    hsla[0] += hue
    drips.push({
      x: x,
      y: y,
      px: x,
      py: y,
      size: random.apply(void 0, dripSize),
      color: hslToStyle(hsla)
    })

  }
}

function moveDrips (config, current) {
  var w = window.innerWidth
  var h = window.innerHeight
  var baseSlope = config.baseSlope;
  var dripStep = config.dripStep;
  var landscapeXYScale = config.landscapeXYScale;
  var landscapeShiftSpeed = config.landscapeShiftSpeed;
  var dripDirection = config.dripDirection;
  var dripDirectionalBias = config.dripDirectionalBias;
  var drips = current.drips;
  var time = current.time;
  var simplex = current.simplex;
  var t = time * landscapeShiftSpeed

  for (var i = 0; i < drips.length; i++) {
    var drip = drips[i]
    // Remove drips if they are off the screen
    if(drip.y > h) {
      drips.splice(i, 1)
      i--
    // Move the drips
    } else {
      // Calculate the slope of the surface:
      //
      //   (x,y,h)     (x2,y,hx)
      //         •----->
      //         |
      //         |
      //         V
      // (x,y2,hy)

      var x = drip.x * landscapeXYScale
      var y = drip.y * landscapeXYScale
      var theta = 0.5 * (simplex.noise3D(x,y,t) + 1)

      theta = lerp(theta, ease(theta), dripDirectionalBias)
      theta = theta * TAU - TAU * dripDirection
      var mx = Math.cos(theta)
      var my = Math.sin(theta)

      drip.px = drip.x
      drip.py = drip.y
      drip.x += dripStep * mx
      drip.y += dripStep * my
    }
  }
}

function drawDrips (config, current) {
  var drips = current.drips;
  var ctx = current.ctx;
  var dripsColor = config.dripsColor;
  var dripStep = config.dripStep;

  drips.forEach(function (drip) {
    var dy = (dripStep + (drip.py - drip.y) / dripStep) / dripStep

    ctx.fillStyle = drip.color
    ctx.fillRect(
      drip.x - drip.size / 2,
      drip.y - drip.size / 2,
      drip.size * dy,
      drip.size * dy
    )
  })
}

function drawScreenDimmer (config, current) {
  var ctx = current.ctx;
  var canvas = current.canvas;
  var random = current.random;
  var dimmingColor = config.dimmingColor;
  var dimmingChance = config.dimmingChance;

  if(random() < dimmingChance) {
    ctx.fillStyle = dimmingColor
    ctx.fillRect(0, 0, canvas.width, canvas.height)
  }
}

function prepCanvasAndGetCtx(config) {
  var backgroundColor = config.backgroundColor;
  var canvas = document.createElement('canvas')
  var ctx = canvas.getContext('2d')
  document.body.appendChild(canvas)
  Object.assign(canvas.style, {
    position: 'absolute',
    top: '0px',
    left: '0px',
  })

  function resize() {
    canvas.width = window.innerWidth * window.devicePixelRatio
    canvas.height = window.innerHeight * window.devicePixelRatio
    canvas.style.width = window.innerWidth + 'px'
    canvas.style.height = window.innerHeight + 'px'
    ctx.scale(
      window.devicePixelRatio,
      window.devicePixelRatio
    )
    console.log('coloring')
    ctx.fillStyle = backgroundColor
    ctx.fillRect(0,0,canvas.width,canvas.height)
  }
  resize(), window.addEventListener('resize', resize, false)

  return [canvas, ctx]
}

